/**
  This is the entrypoint less file that's loaded onto index.html
*/
/* stylelint-disable */
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  background: #ffe0e3;
  box-sizing: border-box;
  font-family: 'Courier New', Courier, monospace;
  transition: background 1s;
}
.main_container {
  padding: 10rem 1rem;
}
@media all and (max-width: 360px) {
  .main_container {
    padding: 5rem 1rem;
  }
}
@keyframes noop {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
@keyframes front_card_load {
  0% {
    opacity: 0;
    transform: translateX(0) translateY(0);
  }
  100% {
    opacity: 1;
    transform: translateX(-1.5rem) translateY(-1.5rem);
  }
}
@keyframes front_card_loop {
  0% {
    opacity: 1;
    transform: translateX(-1.5rem) translateY(-1.5rem);
  }
  50% {
    opacity: 0.5;
    transform: translateX(0) translateY(0);
  }
  100% {
    opacity: 1;
    transform: translateX(-1.5rem) translateY(-1.5rem);
  }
}
@keyframes back_card_load {
  0% {
    opacity: 0;
    transform: translateX(0) translateY(0);
  }
  100% {
    opacity: 0.5;
    transform: translateX(1.5rem) translateY(1.5rem);
  }
}
@keyframes back_card_loop {
  0% {
    transform: translateX(1.5rem) translateY(1.5rem);
  }
  50% {
    transform: translateX(0) translateY(0);
  }
  100% {
    transform: translateX(1.5rem) translateY(1.5rem);
  }
}
.card_stack {
  border-radius: 1rem;
  height: 36rem;
  position: absolute;
  width: 20rem;
  margin: 0 auto;
  position: relative;
}
@media all and (max-width: 360px) {
  .card_stack {
    width: 90%;
    min-width: 16rem;
  }
}
.front_card {
  border-radius: 1rem;
  height: 36rem;
  position: absolute;
  width: 20rem;
  background: transparent;
  border: 2px solid white;
  pointer-events: none;
  transform: translateX(-1.5rem) translateY(-1.5rem);
  z-index: 2;
}
@media all and (max-width: 360px) {
  .front_card {
    width: 90%;
    min-width: 16rem;
  }
}
.secret_card {
  border-radius: 1rem;
  height: 36rem;
  position: absolute;
  width: 20rem;
  overflow: hidden;
  pointer-events: none;
  z-index: 3;
}
@media all and (max-width: 360px) {
  .secret_card {
    width: 90%;
    min-width: 16rem;
  }
}
.secret_card .secret_border_card {
  border-radius: 1rem;
  height: 36rem;
  position: absolute;
  width: 20rem;
  border: 2px solid #ffe0e3;
  transform: translateX(-1.5rem) translateY(-1.5rem);
}
@media all and (max-width: 360px) {
  .secret_card .secret_border_card {
    width: 90%;
    min-width: 16rem;
  }
}
.content_card {
  height: 36rem;
  position: absolute;
  width: 20rem;
  background: white;
  border-radius: 1rem;
  box-shadow: 0px 1px 2px 1px #DDDDDD;
  padding: 2rem;
  padding-right: 3.5rem;
  z-index: 1;
}
@media all and (max-width: 360px) {
  .content_card {
    width: 90%;
    min-width: 16rem;
  }
}
.back_card {
  border-radius: 1rem;
  height: 36rem;
  position: absolute;
  width: 20rem;
  background: white;
  opacity: 0.5;
  transform: translateX(1.5rem) translateY(1.5rem);
  z-index: 0;
}
@media all and (max-width: 360px) {
  .back_card {
    width: 90%;
    min-width: 16rem;
  }
}
.initial .front_card {
  animation: front_card_load 1s;
  animation-fill-mode: forwards;
}
.initial .secret_card .secret_border_card {
  animation: front_card_load 1s;
  animation-fill-mode: forwards;
}
.initial .back_card {
  animation: back_card_load 1s;
  animation-fill-mode: forwards;
}
.loop .front_card {
  animation: front_card_loop 1s;
  animation-fill-mode: forwards;
}
.loop .secret_card .secret_border_card {
  animation: front_card_loop 1s;
  animation-fill-mode: forwards;
}
.loop .back_card {
  animation: back_card_loop 1s;
  animation-fill-mode: forwards;
}
.content {
  transform: translateY(0);
}
.content h1,
.content h2 {
  font-size: 1.5rem;
}
.content .section:not(.active) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
@keyframes content_load {
  0% {
    opacity: 0;
    transform: translateY(1.5rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes content_loop {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  25% {
    opacity: 0;
    transform: translateY(0);
  }
  50% {
    opacity: 0;
    transform: translateY(1.5rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.initial .content {
  animation: content_load 1s;
}
.loop .content {
  animation: content_loop 1s;
}
.menu {
  display: flex;
  justify-content: space-between;
}
.menu li a,
.menu li button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;
  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
  line-height: normal;
  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;
  /* Remove excess padding and border in Firefox 4+ */
  /* my own styles */
  cursor: pointer;
  outline: 0;
  text-decoration: none;
}
.menu li a::-moz-focus-inner,
.menu li button::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.menu li a.active,
.menu li button.active,
.menu li a:focus,
.menu li button:focus,
.menu li a:hover,
.menu li button:hover {
  background-color: pink;
}
